import { mergeDeep } from './../helpers/helpers';
import Interpolator from './Interpolator';
import { defaults } from '../defaults';

//

// params
// detector: a detector with load, detect and configure function
// configs: e.g. :

export default class InterpolatedDetectorMULTI {
  constructor(detectors) {
    // {noser: {detector: NVDDetector(class), config:{}}, mesh: {detector: NVDDetector(class), config:{}}, etc }
    this.detectors = detectors;
    this.loaded = false;
    this.animationFrameId = null;
  }

  async load() {
    let promises = [];
    Object.values(this.detectors).forEach((d) => {
      // Load the models
      promises.push(d.detector.load());
      // await d.detector.load();

      // Add the interpolators
      d.interpolator = new Interpolator(
        (video) => d.detector.detect(video), //slow fn to interpolate between return vals
        d.config.interpolator.stepToward, //interpolation fn
        d.config.interpolator.sensitivity,
        d.config.interpolator.fps
      );
    });
    let p = await Promise.all(promises);
    console.log('all loaded');
    this.loaded = true;
  }

  configure(configs) {
    let detNames = Object.keys(configs);
    detNames.forEach((d) => {
      // get detector
      let dd = this.detectors[d];
      // configure the detector if config
      if (configs[d].detector) {
        this.__configureDetector(configs[d].detector, dd);
      }
      // configure the interpolator if config
      if (configs[d].interpolator) {
        this.__configureInterpolator(configs[d].interpolator, dd);
      }
    });
  }

  // probably bad naming -  actually calls the interpolators (which in turn call the detect fn of detectors as their 'slow' promise)
  detect(video) {
    if (!this.loaded) {
      return {};
    }
    let detections = {};
    Object.keys(this.detectors).forEach((n) => {
      detections[n] = this.detectors[n].interpolator.interpolate(video);
    });

    // let configs = Object.values({ ...this.detectors }).map((v) => v.config);
    let configs = {};
    Object.keys(this.detectors).forEach((n) => {
      configs[n] = this.detectors[n].config;
    });

    return { detections, configs };
  }

  // looping detection
  startDetection(video, loopers = []) {
    const looper = () => {
      let d = this.detect(video);
      //loop any functions hooking into the animation loop
      if (loopers.length) {
        loopers.forEach((fn) => fn(d));
      }
      this.animationFrameId = requestAnimationFrame(looper);
    };
    looper();
  }

  stopDetection() {
    cancelAnimationFrame(this.animationFrameId);
  }
  __configureDetector(config, detector = {}) {
    Object.assign(detector.config.detector, config);

    detector.detector.configure(config);
  }

  // interpolator don't (yet?) have config option so need to instantiate new one
  __configureInterpolator(config, detector = {}) {
    Object.assign(detector.config.interpolator, config);

    detector.interpolator = new Interpolator(
      (video) => detector.detector.detect(video),
      detector.config.interpolator.stepToward,
      detector.config.interpolator.sensitivity,
      detector.config.interpolator.fps
    );
  }
}

// mergeDeep of default configs should be in index.js, not here - > the input should already be a complete config object
// config fn should compare existing configs and new ones. and only update if the key for that detector is in configure fn call object
class InterpolatedDetector {
  constructor(configs, detector) {
    this.detector = detector;
    this.interpolator = null;
    this.configs = mergeDeep({}, defaults, configs);
    // note : current implementation in react always provides a complete configuration object (mergeDeep alrady done)
    // reason: react needs to hold the config state so that sliders etc work
    // so  merge isnt really needed here (currently
  }

  async load() {
    // load and set up detector
    await this.detector.load();
    this.__configureDetector(this.configs.detector);

    //config inits new interpolator (sets this.interpolator = ...)
    this.__configureInterpolator(this.configs.interpolator);
  }

  configure(configuration) {
    // only update configurations which have changed: (slightly smoother UI when configuring)
    //*

    /** TODO This function can be simplified
     * only configure detector/interp if it exists in input
     * (no need for deep conpare-?) - assumes any input is different...
     */
    let detectorConfigChange =
      JSON.stringify(this.configs.detector) !==
      JSON.stringify(configuration.detector);

    let interpolatorConfigChange =
      JSON.stringify(this.configs.interpolator) !==
      JSON.stringify(configuration.interpolator);

    //configure detector
    detectorConfigChange && this.__configureDetector(configuration.detector);

    // init new interpolator (with new configuration)
    interpolatorConfigChange &&
      this.__configureInterpolator(configuration.interpolator);

    //update configs
    this.configs = mergeDeep({}, this.configs, configuration);
  }

  __configureDetector(config) {
    this.detector.configure(config);
  }
  __configureInterpolator(config) {
    this.interpolator = new Interpolator(
      (video) => this.detector.detect(video),
      config.stepToward,
      config.sensitivity,
      config.fps
    );
  }

  detect(video) {
    return this.interpolator.interpolate(video);
  }
}
